import Vue from 'vue';
import Router from 'vue-router';
import Full from '@/containers/Full';
import Full2 from '@/containers/Full2';
import mr from '@/views/mr';
import formMr from '@/views/formMr';
// Views - Pages
import Page404 from '@/views/errorPages/Page404';
import Page500 from '@/views/errorPages/Page500';
const _import = require('./_import_' + process.env.NODE_ENV);

/* login */
const Login = _import('login/index');
Vue.use(Router);

export const constantRouterMap = [
  { path: '/login', component: Login, hidden: true },
  {path: '/pages',
    redirect: '/pages/p404',
    name: 'Pages',
    component: {
      render (c) { return c('router-view') }
      // Full,
    },
    children: [{ path: '404', name: "Page404", component: Page404 },
      {path: '500', name: "Page500", component: Page500},
    ]
  }

]

export default new Router({
  mode: 'hash',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
});

export const asyncRouterMap = [

  {
    path: '/',
    redirect: '/dashboard',
    name: '',
    component: Full,
    hidden: false,
    children: [
      {path: '/dashboard', name: 'Dashboard', icon: 'speedometer', component: _import('Dashboard')},
      {path: '/introduction', name: '', icon: 'thumbsup', component: _import('Introduction')},
      {path: '/components',
        name: 'component',
        redirect: '/components/buttons',
        icon: 'bookmark',
        component: {render (c) { return c('router-view') }},
        children: [ { path: 'buttons', name: 'Buttons', icon: 'social-youtube', component: _import('components/Buttons'), hidden: false, },
          {path: 'hoverbuttons', name: '', icon: 'wand', component: _import('components/HoverButtons')},
          {path: 'alert', name: 'Alert', icon: 'alert', component: _import('components/Alert')},
          {path: 'card', name: 'Card', icon: 'ios-browsers-outline', component: _import('components/Card')},
          {path: 'datepicker', name: 'DatePicker', icon: 'ios-calendar-outline', component: _import('components/DatePicker')},
          {path: 'form', name: 'Form', icon: 'ios-list-outline', component: _import('components/Form')},
          {path: 'modal', name: 'Modal', icon: 'ios-chatbubble-outline', component: _import('components/Modal')},
          {path: 'select', name: 'Select', icon: 'ios-arrow-down', component: _import('components/Select')},
          {path: 'spin', name: 'Spin', icon: 'load-d ', component: _import('components/Spin')},
          {path: 'steps', name: 'Steps', icon: 'ios-checkmark-outline', component: _import('components/Steps')},
          {path: 'timeline', name: 'Timeline', icon: 'android-more-vertical', component: _import('components/Timeline')},
          {path: 'transfer', name: 'Transfer', icon: 'ios-pause-outline', component: _import('components/Transfer')},
          {path: 'timepicker', name: 'Timepicker', icon: 'ios-clock-outline', component: _import('components/Timepicker')},
          {path: 'upload', name: 'Upload', icon: 'ios-cloud-upload-outline', component: _import('components/Upload')},
        ]
      },
      {path: '/charts',
        name: 'echart',
        redirect: '/charts/shopchart',
        icon: 'pie-graph',
        component: {render (c) { return c('router-view') }},
        children: [ { path: 'shopchart', name: '', icon: 'stats-bars', component: _import('charts/ShopChart'), hidden: false, },
          {path: 'radarchart', name: '', icon: 'arrow-graph-up-right', component: _import('charts/RadarChart')},
          {path: 'cakechart', name: '', icon: 'ios-analytics', component: _import('charts/CakeChart')}
        ]
      },
      {path: '/table', name: '', icon: 'ios-paper', component: _import('Table'), meta: { role: ['admin'] }},
      {path: '/jsontree', name: 'JSON', icon: 'merge', component: _import('JsonTree')},
      {path: '/tabledetail/:id', name: 'TableDetail', hidden: true, component: _import('TableDetail')},
      {path: '/tinymce', name: 'Tinymce', icon: "android-document", component: _import('Tinymce')},
      {path: '/markdown', name: 'Markdown', icon: "android-list", component: _import('Markdown')},
      {path: '/mr', name: 'MR', icon: "social-html5", component: mr},
      {path: '/formMr', name: 'FormMr', icon: "social-html5", component: formMr},

    ]
  },

  {
    path: '/home1',
    redirect: '/home1/introduction',
    name: '2',
    component: Full2,
    hidden: false,
    children: [
      {path: '/home1/dashboard', name: 'Dashboard2', icon: 'speedometer', component: _import('Dashboard2')},
      {path: '/home1/introduction', name: '2', icon: 'thumbsup', component: _import('Introduction')},

    ]
  },

  { path: '*', redirect: '/pages/404', hidden: true }

];



